html {
  box-sizing: border-box; }

body {
  font-family: Roboto, sans-serif; }

.container {
  min-width: 320px;
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 15px; }

a {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  text-decoration: underline;
  color: #746554; }

.link {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #000000;
  text-decoration: none; }

/**
 *
 * buttons
 *
 **/
.btn {
  display: inline-block;
  min-width: 180px;
  padding: 6px 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  border: 2px solid;
  outline: none;
  background: transparent;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  cursor: pointer; }

.btn--wide {
  min-width: 280px; }

.btn--large {
  padding: 15px 30px; }

.btn--primary {
  border: 2px solid #3ca826;
  background: #3ca826;
  color: #ffffff; }

.btn--secondary {
  color: #746554; }

/**
*
* form
*
**/
input[type=email],
input[type=number],
input[type=search],
input[type=text],
input[type=tel],
input[type=url],
input[type=password],
textarea {
  appearance: none; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }
  button:focus,
  input:focus,
  optgroup:focus,
  select:focus,
  textarea:focus {
    outline: none; }

label {
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: #36332e; }
  label.error {
    position: absolute;
    right: 0;
    display: block;
    text-align: right;
    font-size: 12px;
    line-height: 16px;
    color: #ec0202; }

input,
select,
textarea, .form__input {
  display: inline-block;
  vertical-align: middle;
  padding: 14px 0 0 0;
  outline: 0;
  border-radius: 0;
  border: none;
  border-bottom: 2px solid #f2f2f2;
  background-color: transparent;
  font-size: 18px;
  font-weight: 300;
  line-height: 34px;
  color: #7f7f7f;
  transition: all 300ms linear; }
  input.error,
  select.error,
  textarea.error, .error.form__input {
    border-color: #ec0202;
    color: #ec0202; }
  input:hover,
  select:hover,
  textarea:hover, .form__input:hover, input:focus,
  select:focus,
  textarea:focus, .form__input:focus {
    outline: none;
    border-bottom: 2px solid #282828; }

.form {
  position: relative;
  width: 100%;
  margin: 0;
  z-index: 10; }

@media screen and (max-width: 991px) {
  .form {
    width: 100%; } }
  .form--feedback {
    max-width: 800px; }

.form__divider {
  padding: 15px; }

.form__header {
  margin: 0 0 20px;
  font-size: 21px;
  font-weight: 400;
  line-height: 24px;
  color: #282828; }

.form__title {
  margin: 0 0 16px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  color: #000000; }

.form__row {
  display: block; }
  .form__row.is--flex {
    display: flex;
    margin-left: -20px; }

.form__field {
  position: relative;
  width: 100%;
  margin-bottom: 40px; }
  .is--flex .form__field {
    margin-left: 20px; }
  .form__field.with--file {
    margin-bottom: 20px; }
  .form__field.is--required:after {
    content: '';
    position: absolute;
    top: 5px;
    right: 8px;
    z-index: 10; }
  .form__field.with-checkbox {
    margin-bottom: 20px; }
  .form__field.mb-10 {
    margin-bottom: 10px; }

.form__field-validation-item {
  display: none;
  font-size: 14px;
  line-height: 21px;
